import React from 'react';
import './home.scss';
import image2_home from './images/image2_home.svg';
import Carousel_home from './carousel_home/carousel_home';
import Tarrifs from './tariffs/tariffs';
import {useSelector} from 'react-redux';
import {Link} from 'react-router-dom';


function Home () {

    //    
    const token = useSelector(state => state.loginReduer.token);


    return (
        <div className='container'>
            <section className='home_section1'>
                <div className='content_left'>
                    <h1 className='h h1'>   <br/> <br/>  </h1>
                    <p className='subtitle'>  ,     PDF   .</p>
                    {token && <Link to="/search" className='button'> </Link>} 
                </div>
                <div className='content_right'></div>
            </section>

            <section className='home_section2'>
                <h2 className='h h2'>  </h2>
                <Carousel_home/>
                <div className='wrapperImage'>
                    <img src={image2_home} alt=" 2" className='image_sec2'/>
                </div>
            </section>

            <section className='home_section3'>
                <h2 className='h h2'> </h2>
                <Tarrifs/>
            </section>
        </div>
    )
} 

export default Home;